footer {
    background: $clr-red;
    a {
        text-decoration: none;
        color: #fff;
    }
    .f-top {
        display: flex;
        justify-content: space-between;
        flex-wrap: nowrap;
        padding-top: 36px;
        padding-bottom: 24px;
        margin-bottom: 24px;
        border-bottom: 1px solid #fff;
        @include mobile414() {
            margin-bottom: 16px;
        }
        .f-wrap {
            display: flex;
            flex-wrap: wrap;
            width: calc(100% - 120px);
            .f-menu {
                width: 100%;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                flex-wrap: nowrap;
                margin-bottom: 18px;
                padding: 12px 0;
                @include fz-sm-16;
                @include pad1024() {
                    padding: 0;
                    margin-bottom: 0;
                }
                @include pad() {
                    display: none;
                }
                li {
                    margin-right: 48px;
                    &:last-of-type {
                        margin-right: 0;
                    }
                }
            }
            .f-contact {
                width: 100%;
                @include pad1024() {
                    margin-top: 24px;
                }
                @include pad() {
                    margin-top: 0;
                }
                a {
                    display: flex;
                    align-items: center;
                    font-size: 36px;
                    line-height: 1.5;
                    font-weight: 400;
                    font-family: 'Roboto', sans-serif;
                    @include pad1024() {
                        @include fz-sm-20;
                    }
                    @include mobile414() {
                        @include fz-sm-16;
                    }
                }
                li {
                    &:first-of-type {
                        margin-bottom: 16px;
                        @include pad1024() {
                            margin-bottom: 9px;
                        }
                        img {
                            margin-right: 23px;
                            @include pad1024() {
                                width: 23px;
                                height: 23px;
                                padding: 4.5px;
                                margin-right: 8px;
                            }
                            @include mobile414() {
                                width: 13px;
                                height: 13px;
                                padding: 3px;
                                margin-right: 6px;
                            }
                        }
                    }
                    &:last-of-type {
                        img {
                            margin-right: 12px;
                            @include pad1024() {
                                width: 32px;
                                height: 32px;
                                margin-right: 8px;
                            }
                            @include mobile414() {
                                width: 20px;
                                height: 20px;
                                margin-right: 5px;
                            }
                        }
                    }
                }
            }
        }
        .f-social {
            display: flex;
            flex-wrap: nowrap;
            li {
                &:nth-of-type(2) {
                    margin: 0 32px;
                }
            }
            @include pad() {
                width: 120px;
                li {
                    &:nth-of-type(2) {
                        margin: 0 24px;
                    }  
                    img {
                        width: 24px;
                        height: 24px;
                    }
                }
            }
        }
    }
    .f-copy {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: nowrap;
        padding-bottom: 27.43px;
        @include fz-sm-14;
        @include pad1024() {
            padding-bottom: 40px;
        }
        @include pad() {
            flex-wrap: nowrap;
            flex-direction: row-reverse;
            align-items: flex-end;
            padding-bottom: 24px;
        }
        .f-copyLogo,.copy-link {
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-wrap: nowrap;
            // @include pad() {
            //     flex-direction: row-reverse;
            //     align-items: flex-end;
            // }
        }
        .f-copyLogo {
            margin-right: 38px;
            @include pad1024() {
                display: none;
            }
            @include mobile414() {
                display: block;
                margin-right: 0;
                img {
                    width: 46px;
                }
            }
        }
        .f-copyCnt {
            width: calc(100% - 118px);
            @include pad() {
                width: 100%;
            }
            > li {
                display: flex;
                justify-content: space-between;
                align-items: center;
            }
            @include pad1024() {
                width: 100%;
                ul {
                    flex-wrap: nowrap;
                }
            }
            @include mobile414() {
                width: calc(100% - 64px);
                ul {
                    flex-direction: row;
                    flex-wrap: wrap;
                }
                li {
                    flex-wrap: wrap;
                }
            }
        }
        .copyright {
            font-family: 'Roboto', sans-serif;
            color: #fff;
            @include fz-sm-16;
            @include mobile414() {
                margin-bottom: 8px;
            }
        }
        .service {
            margin-left: 62px;
            @include pad() {
                margin-left: 24px;
            }
            @include mobile414() {
                width: 100%;
                margin-left: 0;
                margin-top: 8px;
            }
        }        
    }    
}