// common
.i-title {
    text-align: center;
    color: $clr-red-deep;
    @include fz-lg-48;
    @include pad1024() {
        @include fz-lg-32;   
    }
    @include mobile414() {
        @include fz-md-24;
        line-height: 33px;
    }
}

.container {
    @include container;
}

// banner
.banner {
    position: relative;
    background: url(../images/banner.png) center center no-repeat;
    background-size: cover;
    height: 840px;
    @include pad() {
        height: 394px;
    }
    .slogn {
        @include absolute-center;
        text-align: right;
        width: 1200px;
        color: $clr-red-deep;
        h3 {
            font-size: 36px;
            line-height: 1.5;
            font-weight: 400;
        }
        h2 {
            font-size: 64px;
            line-height: 1.5;
            font-weight: 500;
        }
        @include size1280() {
            width: calc(100% - 56px);
            h3 {
                @include fz-sm-20;
            }
            h2 {
                @include fz-lg-48;
                font-weight: 500;
            }
        }
        @include pad() {
            width: calc(100% - 67px);
            h3 {
                @include fz-sm-16;
            }
            h2 {
                @include fz-lg-32;
                line-height: 45px;
                font-weight: 400;
            }
        }
        @include mobile414() {
            width: calc(100% - 24px);
        }
    }
}

// profession
.profession {
    background-color: #AA06010D;
    .i-title {
        padding: 64px 0 64px 0;
        @include pad1024() {
            padding: 40px 12px;
        }
        @include pad() {
            padding: 32px 0 12px 0;
        }
    }
    ul {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: nowrap;
        padding-bottom: 80px;
        @include pad1024() {
            flex-wrap: wrap;
            justify-content: center;
            padding-bottom: 48px;        
        }
    }
    li {
        width: 282px;
        padding: 0 37px;
        @include pad1024() {
            width: 324px;
            margin-right: 24px;
            margin-bottom: 12px;
            box-sizing: border-box;
            padding: 0 50px 8px 50px;
            &:nth-of-type(1) {
                order: 3;
                margin-bottom: 0;
            }
        
            &:nth-of-type(2) {
                order: 1;
            }
        
            &:nth-of-type(3) {
                order: 2;
                margin-right: 0;
                .professionImg {
                    @include pad1024() {
                        margin-top: 20px;
                        margin-bottom: 20.81px;
                    }
                }
            }
        
            &:nth-of-type(4) {
                order: 4;
                margin-right: 0;
                margin-bottom: 0;
                .professionImg {
                    @include pad() {
                        margin-top: 32px;
                        margin-bottom: 27.01px;
                    }
                }
                p {
                    @include pad() {
                        margin-bottom: 0;
                    }
                }
            }
        }
        @include pad() {
            width: 324px;
            margin-right: 0;
            margin-bottom: 0;
        }
        @include mobile414() {
            width: 224px;
            padding: 0;
        }
        .professionImg {
            height: 100px;
            margin-top: 16px;
            margin-bottom: 40px;
            display: flex;
            justify-content: center;
            align-items: center;
            @include pad1024() {
                margin-top: 20px;
                margin-bottom: 16px;
            }
            @include pad() {
                height: auto;
            }
            img {
                margin: 0 auto;
                @include pad() {
                    width: 80px;
                }
            }
        }
        h4 {
            text-align: center;
            margin-bottom: 4px;
            color: $clr-red-deep;
            @include fz-md-24;
            @include pad1024() {
                @include fz-sm-20;
                font-weight: 500;
            }
            @include pad() {
                margin-bottom: 8px;
            }
        }
        p {
            @include fz-sm-16;
            @include mobile414() {
                margin-bottom: 8px;
            }
        }
    }
}

// classic
.classic {
    .container {
        @include pad() {
            padding-right: 0;
        }
        @include mobile667() {
            padding-right: 48px;
        }
        @include mobile414() {
            padding-right: 12px;
        }
    }
    .i-title {
        margin: 64px 0 60px 0;
        @include pad1024() {
            margin: 40px 0 32px 0;
        }
        @include pad() {
            margin: 24px 0 16px 0;
        }
    }
    li {
        margin-bottom: 32px;
        @include pad() {
            margin-bottom: 8px;
        }
        .classic-type1,
        .classic-type2,
        .classic-type3 {
            text-decoration: none;
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            flex-wrap: nowrap;
            position: relative;
        }
        .classic-type1 {
            .classicName {
                img {
                    @include pad() {
                        height: 25.16px;
                    }
                }
            }            
        }
        .classic-type2 {
            flex-direction: row-reverse;
            @include pad() {
                flex-direction: row;
            }
        }
        .classic-type3 {
            margin-bottom: 80px;
            @include pad1024() {
                margin-bottom: 48px;
            }
            @include pad() {
                margin-bottom: 24px;
            }
        }
        .classicImg {
            @include pad1024() {
                max-width: 520px;
            }
            @include pad() {
                max-width: 410.85px;
            }
            @include mobile667() {
                max-width: 100%;
            }
            .imgColorful {
                display: none;
            }
            .imgGray {
                display: block;
            }
            &:hover {
                .imgColorful {
                    display: block;
                }
                .imgGray {
                    display: none;
                }                 
            }
        }
        .classicName {
            display: flex;
            align-items: center;
            margin: 0 24px;
            font-family: 'Roboto', sans-serif;
            @include pad1024() {
                margin: 0 auto;
            }
            @include mobile667() {
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
            }
            .redIcon,
            .whiteIcon {
                margin-right: 8px;
                @include pad() {
                    height: 32px;
                    position: absolute;
                    transform: translateX(-100%);
                    padding-right: 8px;
                    margin-right: 0;
                }
                @include mobile667() {
                    position: relative;
                    transform: translateX(0);
                }
            }
            .redIcon {
                display: block;
                @include mobile667() {
                    display: none;
                }
            }
            .whiteIcon {
                display: none;
                @include mobile667() {
                    display: block;
                }
            }
            h2 {
                margin: 0;
                font-style: italic;  
                color: $clr-red;
                @include fz-lg-48;
                @include pad() {
                    font-size: 28px;
                    line-height: 1.5;
                }
                @include mobile667() {
                    color: #fff;
                }          
            }
        }
    }
}

// joint
.joint {
    background: url('../images/nick-owuor-astro-nic-visuals-54Q_HvMWtVE-unsplash.png') center top no-repeat;
    @include pad1024() {
        background-size: auto 1221px;
    }
    @include mobile414() {
        background-size: auto 1300px;
    }
    @include mobile() {
        background-size: auto 1096px;
    }
    .i-title {
        padding: 64px 0 60px 0;
        color: #fff;
        @include pad1024() {
            padding: 40px 0 32px 0;
        }
        @include mobile414() {
            padding: 24px 0 16px 0;
        }
    }
    img {
        @include pad1024() {
            height: 100%;
            object-fit: cover;
        }
    }
    .jointWrap {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        font-family: 'Roboto', sans-serif;
        max-width: 1539px;
        margin: 0 auto;
        margin-bottom: 144px;
        @include size1680() {
            padding: 0 48px;
        }
        @include pad1024() {
            margin-bottom: 40px;        
        }
        @include pad() {
            padding: 0 12px 0 11px;
        }
        @include mobile414() {
            margin-bottom: 0;
        }
    }
    .wrap-more {
        display: flex;
        margin-bottom: 60px;
        @include pad1024() {
            max-width: 672px;
            margin: 0 auto;
            margin-bottom: 40px;
        }
        @include mobile414() {
            margin-bottom: 20px;
        }
        @include mobile() {
            max-width: 327px;
        }
    }
    .wrap-moreImg {
        max-width: 573px;
        max-height: 840px;
        margin-right: 8px;
        @include pad1024() {
            max-width: 251px;
            max-height: 367px;
            margin-right: 3px;
        }
        @include mobile() {
            max-width: 156px;
            max-height: 226px;
            margin-right: 0;
        }
    }
    .wrap-more-info {
        display: flex;
        flex-wrap: wrap;
        max-width: 958px;
        @include pad1024() {
            max-width: 418px;
        }
        @include mobile() {
            max-width: 169px;
        }
    }
    .wrap-more-infoImg {
        margin-bottom: 8px;
        @include pad1024() {
            max-width: 418px;
            max-height: 198px;
            margin-bottom: 3px;
        }
        @include mobile() {
            max-width: 196px;
            max-height: 81px;
            margin-bottom: 2px;
        }
    }
    .wrap-more-info-cnt {
        display: flex;
        flex-wrap: nowrap;
    }
    .wrap-more-info-cntImg {
        max-width: 132px;
        margin-right: 8px;
        @include pad1024() {
            display: block;
            max-width: 58px;
            max-height: 166px;
            margin-right: 3px;
        }
        @include mobile() {
            display: none;
        }
    }
    .wrap-more-info-cnt-bottom {
        display: flex;
        justify-content: flex-end;
        flex-wrap: wrap;
        max-width: 818px;
        @include pad1024() {
            max-width: 356px;
        }
        @include mobile() {
            justify-content: flex-start;
        }
    }
    .wrap-more-info-cnt-bottomImg {
        margin-bottom: 8px;
        @include pad1024() {
            max-width: 356px;
            max-height: 89px;
            margin-bottom: 0;
        }
        @include mobile() {
            max-width: 169px;
            max-height: 42px;
            margin-bottom: 2px;
        }
    }
    .wrap-more-info-cntImg-moible {
        display: none;
        @include mobile() {
            display: block;
            margin-right: 2px;
            width: 32px;
            max-height: 99px;
        }
    }
    .wrap-more-info-bottom-link {
        position: relative;
        @include mobile() {
            display: flex;
            align-items: center;
            width: 135px;
            max-height: 99px;
        }
    }
    .linkImg {
        margin-right: 84px;
        @include pad1024() {
            margin-right: 48px;
            img {
                width: 147px;
                height: 77px;                
            }
        }
        @include mobile() {
            margin-right: 0;
            img {
                width: 102px;
                height: 53px;
            }
        }
    }
    .linkCnt {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-wrap: nowrap;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate3d(0, -50%, 0);
        background: #fff;
        color: $clr-red;
        width: 315px;
        height: 105px;
        @include fz-xl-72;
        @include pad1024() {
            font-size: 40px;
            line-height: 30px;
            width: 178px;
            height: 59px;
        }
        @include mobile() {
            font-size: 26px;
            line-height: 20px;
            width: 124px;
            height: 41px;            
        }
        a {
            position: relative;
            text-decoration: none;
            color: $clr-red;
            &:before,
            &:after {
                content: "MORE";
                color: $clr-red;
                position: absolute;
                top: 0;
                left: 0;
                transform: translateY(0);
                transition: transform 600ms ease-in-out;
                @include fz-xl-72;
                @include pad1024() {
                    @include fz-lg-40;
                }
                @include mobile() {
                    font-size: 26px;
                    line-height: 20px;           
                }
            }
            &:before {
                opacity: 0.6;
            }
            &:after {
                opacity: 0.4;
            }
        }
        img {
            padding: 29px 19px 28px 17px;
            transform: translateX(0);
            transition: transform 600ms ease-in-out;
            @include pad1024() {
                max-height: 27.1px;
                padding: 16.38px 10.86px 15.52px 10.04px;
            }
            @include mobile() {
                max-height: 18px;
                padding: 12px 14px 11px 8px;
            }
        }
        &:hover {
            a {
                &:before {
                    transform: translateY(-95px);
                    @include pad1024() {
                        transform: translateY(-55px);
                    }
                }
                &:after {
                    transform: translateY(-160px);
                    @include pad1024() {
                        transform: translateY(-90px);
                    }
                } 
            }
            img {
                transform: translateX(15px);
            }
        }
    }
    .wrap-double,
    .wrap-youth {
        position: relative;
        @include size1680() {
            width: 48.214826%;
        }
        @include pad1024() {
            width: 40%;
            max-height: 280px;
        }
        @include mobile414() {
            width: 100%;
            margin-bottom: 20px;
        }
        &:before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            background: rgba(225,225,225,.5);            
        }
        a {
            text-decoration: none;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;            
        }
        h2 {
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate3d(0,-50%,0);
            width: 100%;
            text-align: center;
            color: $clr-red;
            @include fz-xl-72;
            @include pad1024() {
                @include fz-lg-32;
                line-height: 1.28;
            }
        }
    }
    .wrap-double {
        margin-right: 30px;
        @include pad1024() {
            margin-right: 24px;
        }
        @include mobile414() {
            margin-right: 0; 
        }
    }
    .wrap-youth {
        @include mobile414() {
            margin-right: 0;
            margin-bottom: 24px;
        }
    }
}

// recommend
.recommend {
    .i-title {
        padding-top: 0;
        padding-bottom: 60px;
        @include pad1024() {
            padding-bottom: 32px;
        }
        @include mobile414 {
            padding-bottom: 16px;
        }
    }
    ul {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: nowrap;
        margin-bottom: 80px;
        @include pad1024() {
            flex-wrap: wrap;
            padding: 0 48px;
            margin-bottom: 40px;            
        }
        @include pad() {
            padding: 0;
            margin-bottom: 40px;
        }
        @include mobile414() {
            margin-bottom: 24px;
        }
    }
    li {
        width: 23.5%;
        @include pad1024() {
            width: 48.214826%;
            &:nth-of-type(-n+2) {
                margin-bottom: 24px;
            }
        }
        @include mobile414() {
            width: 100%;
            margin-bottom: 16px;
            &:nth-of-type(-n+2) {
                margin-bottom: 16px;
            }
            &:last-of-type {
                margin-bottom: 0;
            }
        }
        img {
            width: 100%;
            @include pad1024() {
                height: 256px;
                object-fit: cover;                
            }
        }
    }
    .recommendCnt {
        background: #fff;
        border: 1px solid #DEE2E6;
        border-top: none;
        padding: 16px;
        font-family: 'Roboto', sans-serif;
        @include fz-sm-16;
        b {
            display: block;
            margin-bottom: 16px;
            line-height: 1.3125;
            font-weight: 500;            
        }
        p {
            margin-bottom: 16px;
        }
        em {
            display: block;
            line-height: 1.3125;
            color: #707070;            
        }
    }
}

// contact
.contact {
    background-color: #AA06010D;
    .container {
        max-width: 792px;
        @include pad1024() {
            max-width: 498px;
        }
        @include mobile414() {
            max-width: 100%;
            padding: 0 24px;
        }
    }
    .i-title {
        padding: 64px 0 24px 0;
        @include pad1024() {
            padding: 40px 0 16px 0;
        }
        @include mobile414() {
            padding: 24px 0 8px 0;
        }
    }
    p {
        margin-bottom: 20px;
        @include fz-sm-20;
        @include pad1024() {
            margin-bottom: 16px;
            @include fz-sm-16;
        }
        @include mobile414() {
            margin-bottom: 8px;
        }
    }
    form {
        @include fz-sm-16;
    }
    li {
        margin-bottom: 16px;
        @include pad1024() {
            margin-bottom: 12px;
        }
        label {
            display: block;
            margin-bottom: 4px;
        }
        input[type="text"],
        textarea {
            width: 100%;
            background: none;
            padding: 14px 16px;
            border: 1px solid #000;
            box-sizing: border-box;
            @include fz-sm-16;
            @include pad1024() {
                padding: 16.5px 16px;
            }
            @include mobile414() {
                padding: 7px 12px;
            }
        }
        textarea {
            height: 136px;
            @include mobile414() {
                height: 100px;
            }
        }
    }
    .check {
        display: flex;
        align-items: center;
        margin-bottom: 42px;
        @include pad1024() {
            align-items: flex-start;
            margin-bottom: 24px;
        }
        @include mobile414() {
            margin-bottom: 20px;
        }
        input[type="checkbox"] {
            position: relative;
            appearance: none;
            -webkit-appearance: none;
            width: 24px;
            height: 24px;
            border: 1px solid #000;
            background: none;
            cursor: pointer;
            @include mobile414() {
                width: 17.63px;
                height: 17.63px;
            }
            &:before {
                position: absolute;
                content: '';
                display: block;
                width: 16px;
                height: 16px;
                background: #000;
                top: 50%;
                left: 50%;
                transform: translate3d(-50%,-50%,0);
                opacity: 0;                
            }
            &:checked {
                &:before {
                    opacity: 1;
                }
            }
        }
        label {
            margin-bottom: 0;
            margin-left: 4px;
            width: fit-content;
            @include mobile414() {
                @include fz-sm-14;
            }
        }
    }
    .submit {
        text-align: center;
        margin-bottom: 0;
        padding-bottom: 92px;
        @include pad1024() {
            padding-bottom: 56px;
        }
        @include mobile414() {
            padding-bottom: 48px;
        }
        input[type="button"] {
            appearance: none;
            -webkit-appearance: none;
            padding: 16px 58px;
            background: $clr-red;
            border: none;
            cursor: pointer;
            color: #fff;
            @include fz-sm-16;
            &:hover {
                background: $clr-red-deep;                
            }
            @include pad1024() {
                padding: 7px 36px;
            }
        }
    }
}