.page-banner {
	position: relative;
	background: url(../images/72.png) center center no-repeat;
	background-size: cover;
	height: 304px;
	display: flex;
	align-items: flex-end;
	@include mobile414() {
		height: 143px;
	}
	.page-slogn {
		width: 100%;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		@include container;
		h2 {
			font-size: 64px;
			line-height: 90px;
			font-weight: 500;
			margin-right: 10.1px;
			@include pad() {
				margin-right: 16px;
				@include fz-lg-48;
				line-height: 67px;
				font-weight: 500;
			}
			@include mobile414() {
				margin-right: 8px;
				@include fz-lg-28;
				line-height: 40px;
				font-weight: 500;
			}
		}
		img {
			@include pad() {
				width: 48px;
			}
			@include mobile414() {
				width: 24px;
			}
		}
	}
}
.accordions {
	margin: 88px 0 156px 0;
	@include pad() {
		margin: 40px 0 104px 0;
	}
	h3 {
		margin-bottom: 16px;
		@include fz-lg-32;
		font-weight: 500;
		line-height: 45px;
		@include pad() {
			margin-bottom: 32px;
			@include fz-lg-28;
			line-height: 40px;
			font-weight: 500;
		}
		@include mobile414() {
			margin-bottom: 16px;
			@include fz-sm-20;
			line-height: 1.4;
			font-weight: 500;
		}
	}
	.accordion-item {
		border: 1px solid #000;
		border-bottom: none;
		&:last-of-type {
			border-bottom: 1px solid #000;
		}
		.accordion-title {
			position: relative;
			cursor: pointer;
			padding: 22px 38px;
			transition: all 0.4s;
			border-bottom: none;
			@include pad() {
				padding: 16px 24px;
			}
			@include mobile414() {
				padding: 16px;
			}
			h4 {
				margin: 0;
				display: flex;
				align-items: center;
				justify-content: space-between;	
				color: #000;
				@include fz-sm-20;
				line-height: 1.4;
				font-weight: 700;
				@include pad() {
					@include fz-sm-16;
					line-height: 22px;
					font-weight: 700;
				}
			}
			.fa-chevron-down {
				transform: rotate(0);
				transition: 0.4s;				
			}
			.chevron-top {
				transform: rotate(-180deg);
			}
			svg {
				font-size: 20px;
				@include pad() {
					font-size: 18px;
				}
			}
		}
		.accordion-content {
			display: none;
			line-height: 1.7;
			padding: 22px 38px;
			background-color: #F2F2F2;
			border-top: 1px solid #000;
			@include pad() {
				padding: 16px 24px;
			}
			@include mobile414() {
				padding: 16px;
			}
			&.active {
				display: block;
			}
			p{
				margin: 0;
				@include fz-sm-16;
				@include pad() {
					@include fz-sm-14;
					line-height: 21px;
				}
			}
		}
	}
}