header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    background:$clr-red;
    padding: 30px 88px 29px 88px;
    position: relative;
    @include pad() {
        padding: 0 16px;
        height: 50px;
    }
    .logo {
        position: relative;
        @include pad() {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            z-index: 9999999;
        }
        img {
            @include pad() {
                width: 48px;
            }
        }
    }
    .menu {
        @include pad() {
            display: none;
        }
        ul {
            @include fz-sm-20;
        }
        li {
            display: inline-block;
            margin-right: 64px;
            &:last-of-type {
                margin-right: 0;
            }
            @include pad() {
                margin-right: 40px;
            }
        }
        a {
            color: #fff;
            text-decoration: none;
        }
    }
    .openbtn {
        display: none;
        background: none;
        border: none;
        padding: 0;
        cursor: pointer;
        svg {
            color: white;
            font-size: 20px;
            padding: 4px;
        }
        @include pad() {
            display: block;
        }
    }
    .sidepanel  {
        width: 0;
        position: fixed;
        z-index: 1;
        top: 0;
        left: 0;
        background: $clr-red;
        overflow-x: hidden;
        transition: 0.2s;
        li {
            text-align: center;
            border: 1px solid #fff;
            padding: 16px 0;
            @include fz-sm-20;
            a {
                padding: 0;
            }
        }
        a {
            color: #fff;
            text-decoration: none;
            display: block;
            transition: 0.1s;
            overflow:hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
        .closebtn {
            padding: 11px 16px;
            svg {
                font-size: 20px;
                color: #fff;
                padding: 4px;
            }           
        }
    }
}