// 容器最大寬度
@mixin container {
    max-width: 1200px;
    margin: 0 auto;
    @include size1280() {
        padding: 0 48px;
    }
    @include mobile414() {
        padding: 0 12px 0 11px;
    }
}

// 文字
@mixin fz-sm-14 {
    font-size: 14px;
    line-height: 1.5;
    font-weight: normal;
}

@mixin fz-sm-16 {
    font-size: 16px;
    line-height: 1.5;
    font-weight: normal;
}

@mixin fz-sm-20 {
    font-size: 20px;
    line-height: 1.5;
    font-weight: normal;
}

@mixin fz-md-24 {
    font-size: 24px;
    line-height: 1.5;
    font-weight: 500;
}

@mixin fz-lg-28 {
    font-size: 28px;
    line-height: 1.5;
    font-weight: bold;
}

@mixin fz-lg-32 {
    font-size: 32px;
    line-height: 1.5;
    font-weight: bold;
}

@mixin fz-lg-40 {
    font-size: 40px;
    line-height: 30px;
    font-weight: bold;
}

@mixin fz-lg-48 {
    font-size: 48px;
    line-height: 1.5;
    font-weight: bold;
}

@mixin fz-xl-72 {
    font-size: 72px;
    line-height: 1.5;
    font-weight: bold;
}

// absolute 定位
// 置中
@mixin absolute-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%,-50%,0);
}